.About {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  margin-top: 0;
  min-height: 100vh;
	height: 100%;
	opacity:0;
	-webkit-animation: fadeIn 1.0s cubic-bezier(0.4, 0.0, 0.2, 1) forwards;
	        animation: fadeIn 1.0s cubic-bezier(0.4, 0.0, 0.2, 1) forwards;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.bg {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	height: 100vh;
	width: 100vw;
	text-align: center;
	position: relative;
	z-index: -2;
	background-image: 
	-webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, 0.5)),
      to(rgba(0, 0, 0, 0.5))
    ),
		url('../../../images/matthewchristine.jpg');
	background-image: 
	-webkit-linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
		url('../../../images/matthewchristine.jpg');
	background-image: 
	-o-linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
		url('../../../images/matthewchristine.jpg');
	background-image: 
	linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
		url('../../../images/matthewchristine.jpg');

	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.aboutText {
	border: 1px solid;
	overflow: auto;
	color: #FFF;
	font-size: 22px;
	font-family: 'Muli';
	padding: 0.5rem;
	height: 55vh;
	width: 50%;
	display: inline;
	line-height: 6vh;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #818181;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    background: #FAFAFA;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #818181;
}


