.Navigation {
	font-size: 21px;
}

.navbar {
	width: 100%;
	position: fixed;
	margin-top: 6px;
	z-index: 1;
}

.navbar_navigation {
	height: 100%;
	padding: 0 1rem;
	-ms-flex-pack: center;
	    justify-content: center;
}

.navbar_navigation-items ul {
	list-style: none;
	margin: 0;
	padding-top: 1.5vh;
	padding-left: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: #FAFAFA;
}

.mid {
	padding: 0 4rem;
}

.navbar_navigation-items li a {
	display: inline-block;
	text-decoration: none;
	font-family: 'Montserrat';
	font-weight: bold;
	color: #FAFAFA;
}

.leftedge {
	padding-right: 4rem;
	padding-left: 1.5rem;
}

.rightedge {
	padding-left: 4rem;
	padding-right: 1.5rem;
}

.navbar_navigation-items a:hover {
	color: rgba(17, 17, 17, 0.7);
	-webkit-animation: bounce 1.5s infinite;
	        animation: bounce 1.5s infinite;
	font-weight: bold;
	-webkit-transition: color 0.3s;
	-o-transition: color 0.3s;
	transition: color 0.3s;
}

.navbar_navigation-items li a.active {
	color: rgba(17, 17, 17, 0.7);
}

.toggle-button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	height: 24px;
	width: 30px;
	background: transparent;
	border: none;	
	cursor: pointer;
	outline: none;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-ms-flex-align: center;
	    align-items: center;
	position: absolute;
	z-index: 2;
}

.buttonline {
	width: 24px;
	height: 2px;
	background: #FAFAFA;
}

.hamburgerButton {
	left: 0;
	padding-left: 1vw;
	padding-top: 1vh;
	position: absolute;
	-ms-flex-pack: center;
	    justify-content: center;
	outline: none;
}

.NavMenu {
	height: 45vh;
	width: 100%;
	background: rgba(64, 64, 64, 0.95);
	-webkit-box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
	        box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
	position: absolute;
	top: 0;
	left: 0;
	text-align: center;
	z-index: 1;
}

.NavMenu ul {
	padding: 0;
	-ms-flex-align: center;
	    align-items: center;
}

.NavMenu-entering {
	-webkit-transform: translateY(0%);
	    -ms-transform: translateY(0%);
	        transform: translateY(0%);
	-webkit-transition: -webkit-transform 0.3s ease-out;
	transition: -webkit-transform 0.3s ease-out;
	-o-transition: transform 0.3s ease-out;
	transition: transform 0.3s ease-out;
	transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.NavMenu-exiting {
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
	-webkit-transition: -webkit-transform 0.3s ease-out;
	transition: -webkit-transform 0.3s ease-out;
	-o-transition: transform 0.3s ease-out;
	transition: transform 0.3s ease-out;
	transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.NavMenu-exited {
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
}

.NavMenu ul li {
	margin-left: 0;
	margin-right: 0;
	margin-top: 3vh;
	margin-bottom: 5vh;
}

.NavMenu ul li a {
	display: inline-block;
	text-decoration: none;
	font-family: 'Montserrat';
	color: rgba(17, 17, 17, 1);
	color: #FAFAFA;
	font-weight: 600;
	font-size: 1.2em;

}

.NavMenu ul li a.active {
	color: #A0A0A0;
}

.NavMenu ul li a:hover {
	color: #A0A0A0;
	-webkit-animation: bounce 1.5s infinite;
	        animation: bounce 1.5s infinite;
}

@media (min-width: 1024px) {
	.navbar_navigation-items li a {
		margin-left: 10px;
		margin-right: 15px;
	}
}

@media (max-width: 840px) {
	.navbar_navigation-items {
		display: none;
	}
	.NavMenu ul li a {
		font-size: 20px;
	}
	.NavMenu {
		padding-bottom: 10px;
	}
}

@media (min-width: 842px) {
	.hamburgerButton {
		display: none;
	}

	.NavMenu {
		display: none;
	}
}



